import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeVie from '../components/home/HomeVie.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeVie
  },
  {
    path: '/city',
    name: 'city',
    component: ()=>import('../components/city/CityVie.vue')
  },
  {
    path: '/details',
    name: 'details',
    component: ()=>import('../components/details/DetailsVie.vue')
  },
  {
    path: '/mymine',
    name: 'mymine',
    component: ()=>import('../components/mymine/HeaderVdd.vue')
  },
  {
    path: '/ordervie',
    name: 'ordervie',
    component: ()=>import('../components/ordervie/OrderVie.vue')
  },
 
  
]

const router = new VueRouter({
  mode:'hash',
  base: process.env.BASE_URL,
  routes
})

export default router


