<template>
   <div>
       <div class="banner">
           <transition 
           enter-active-class='bounceInLeft'
           leave-active-class='fadeOut'>
           <div class='benner-left animated' v-show="toshow">
               <div>
                   <NanVie></NanVie>
                   <Nanbody></Nanbody>
               </div>
           </div>
           </transition>
           <div class="banner-header">
               <div class="banner-top">
                 <div class="top-left" @click="toCity">
                     {{cityName}}
                     <i class="iconfont">&#xe69f;</i>
                 </div>
                 <div class="top-search">
                    <label>泰国</label>
                     <i class="iconfont">&#xe629;</i>
                     <input type="text" placeholder="请输入消息" v-model="shuji" clearable filterable class="top-input">
                     <p>{{shuji}}</p>
                     
                 </div>
                 <div class="top-right">
                     <img src="@/assets/img/1.png" 
                      @click="toShow">
                 </div>
               </div>
               <div class='banner-swiper'>
        <!--swiper -->
  <swiper :options="swiperOption">
    <swiper-slide><img src="@/assets/img/banner2.jpg" alt="">
    </swiper-slide>
    <swiper-slide><img src="@/assets/img/banner1.jpg" alt="">
    </swiper-slide>
    <swiper-slide><img src="@/assets/img/banner3.jpg" alt="">
    </swiper-slide>
    <div class="swiper-pagination" slot="pagination">
    </div>
  </swiper>
               </div>
           </div>
       </div>
   </div>

</template>

 
<script>
import Nanbody from './NavbodyVie.vue'
import NanVie from './NavVie.vue'
import {mapState} from 'vuex'
export default {
    components:{
        NanVie,
        Nanbody
    },
    data () {
        
        return{
            shuji:"",
            toshow: false,
            //swiper
            swiperOption:{
                loop:true,
                autoplay:true,
                pagination :{
                     el:".swiper-pagination",
                }
            }
        }
    },
    computed:{
        ...mapState(['cityName'])
    },
     methods: {
         toShow() {
           this.toshow=!this.toshow
         },
         toCity(){
             this.$router.push({
                 path:"/city"
             })
         },
         
    },
    
    
}

</script>

<style scoped>
.top-left{
    cursor: pointer;
}
.benner-left{
    position: absolute;
    left: 0;
    top: 0;
    z-index: 999;
    width: 6.6666666rem;
    height: 100%;
    background:rgba(100, 0, 50, 0.5) ;
   
}
.banner-header{
    background: #f5f5f5;
    width: 100%;
    height: 200px;
    position: relative;
}
.banner-top{
    position: absolute;
    left: 0;
    top: 1rem;
    z-index: 2;
    height: .8rem;
    width: 100%;
    display: flex;
    justify-content: center;
    align-content: center;
    width: 700px;

}
.top-left{
    padding: 0 .3rem;
    color: #fff;
    font-size: 10px;
}
.top-search{
    flex: 1;
    color: #333;
    border-radius: .3rem;
    font-size: 10px;
    padding: 4px .3rem;
    background: white;

}
.top-right{
    height: .5rem;
    width: .5rem;
    width: -200px;
}
.top-right img{
    height: .5333333rem;
    width: .5333333rem;
    margin-left: 300px;
}
.banner-swiper{
    width: 100%;
    height: 5.6rem;
}
.banner-swiper img{
    width: 100%;
    height: 235px;
}

.banner-swper{
    width: 100%;
    height: 100%;
}
.swiper-pagination > .swiper-pagination-bullet-active {
    background: red}
.top-input{
    width: 500px;
    height: 32px;
}
</style>