<template>
  <div class="scroll">
      <div class="scroll-content">
        <!--滚动-->
        <div class="scroll-right">
            <ul :class="{aniamtedTop}">
                <li v-for="item in scrollList" :key='item.id'>
                    <img src="@/assets/img/scroll1.jpg" alt="">
                    {{item.title}}
                </li>
            </ul>
        </div>
      </div>
  </div>
</template>

<script>
export default {
   data () {
       return {
           aniamtedTop:false,
           scrollList:[
               {
              id:0,
              title:'湖光岩风景区位于湛江市区西南部约19公里处,构造地质地貌等多种地质遗迹。',
           },
           {
              id:1,
              title:'停在霞山观海长廊上的巨型客轮“海上城市”是一大景观。船长160米，高近20米，海湾美景。',
           },
           {
              id:2,
              title:'南三岛与东海岛、硇洲岛等许多岛屿组成湛江港的外围防护栏。拥有阳光、沙滩、大海、森林、',
           },
           ]
       }
   },
   mounted () {
       setInterval(this.showScroll,3000);
   },
   methods:{
       showScroll(){
           this.aniamtedTop=true,
           setTimeout(()=>{
               this.scrollList.push(this.scrollList[0]);
               this.scrollList.shift();
               this.aniamtedTop=true
           })
       }
   }
}
</script>

<style scoped>
/*滚动*/ 
.aniamtedTop{
    transition: all 1s;
    height: 20px;
    position: relative;
}
.scroll{
   padding: .3rem 0;
   height: 100px;
   overflow: hidden;
}
.scroll-content{
    background: url('@/assets/img/scroll.jpg') no-repeat;
    background-size: 100%;
    width: 100%;
    height:2.66666666rem;
    position: relative;
}
.scroll-right{
    position: absolute;
    right: 0px;
    width: 16.88888888rem;
    height: 2rem;
    overflow: hidden;
}
li{
   font-size: .35rem;
   color: #333;
   line-height: .66666666rem; 
   /* 溢出隐藏*/
  
}
li img{
    width:8px;
    height: 8px;
}
</style>