import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
let defaultCticy='北京';
defaultCticy=localStorage.city
export default new Vuex.Store({
  state: {
    cityName:defaultCticy
  },
  getters: {
  },
  mutations: {
    changeCity(state,city){
      state.cityName=city
      //本地存储
      localStorage.city=city;
    }
  },
  actions: {
  },
  modules: {
  }
})
