<template>
  <div class="home">
      <BnenrVie></BnenrVie>
      <YcosVie></YcosVie>
      <TabsVie></TabsVie>
      <ScrollVie></ScrollVie>
      <SwiperVie></SwiperVie>
      <SpikeVie :spikeList='spikeList'></SpikeVie>
      <LikeVie :likeList='likeList'></LikeVie>
      <FooterVie></FooterVie>
      <RcomeBie></RcomeBie>
  </div>
</template>

<script>
import {mapState} from 'vuex'
import BnenrVie from './BnennrVie.vue'
import YcosVie from './YcosVie.vue'
import TabsVie from './TabsVie.vue'
import ScrollVie from './ScrollVie.vue'
import SwiperVie from './SwiperVie.vue'
import SpikeVie from './SpikeVie.vue'
import LikeVie from './LikeVie.vue'
import FooterVie from './FooterVie.vue'
import RcomeBie from './RcomeBie.vue'
export default {
  components:{
      BnenrVie,
     YcosVie,
     TabsVie,
     ScrollVie,
     SwiperVie,
     SpikeVie,
     LikeVie ,
     FooterVie,
     RcomeBie
  },
  data () {
      return {
          spikeList:[],
          likeList:[],
          changeCity:''
      }
  },
  computed:{
    ...mapState(["cityName"])
  },
  mounted(){
    //现在城市与以前数据
    this.changeCity=this.cityName;
    this.http();
  },
  methods:{
    http(){
      let That=this
      this.axios.get("http://localhost:8080/ipa/dataHome.json")
      .then((res)=>{
        let data=res.data.data;
       // console.log(That.cityName,res.data)
        data.forEach((item)=>{
               if(item.city==That.cityName){
                 //console.log(item)
                 That.spikeList=item.spikeList;
                   That.likeList=item.likeList
               }
        })
                
       
       
       
      })
      
    }
  },
  //keep alive 的生命周期
  activated(){
    //原有数据不等于现在的数据就请求数据
    if(this.changeCity!=this.cityName){
      this.http();
      this.changeCity=this.cityName;
    }
  }
}
</script>

<style scoped>
 .home{
   overflow: hidden;
 }
</style>