<template>
  <div class="spike">
       <!--swiper -->
  <swiper :options="swiperOption">
    <swiper-slide v-for="item in spikeList" :key="item.id">
        <div class="spike-content"
        @click="todetails(item)">
            
        
        <div class="spike-top">
          <img src="@/assets/img/6.jpg">
          <span>距离结束:</span>
         <count-down 
            
            :currentTime="item.startTime"
            :startTime="item.startTime"
            :endTime="item.endTime"
            :dayTxt="'天'"
            :hourTxt="'小时'"
            :minutesTxt="'分钟'"
            :secondsTxt="'秒'">
            </count-down>
        </div>
        <div class="spike-botton">
             <img src="@/assets/img/莲花山1.jpeg">
            <img src="@/assets/img/莲花山2.jpeg">
            <img src="@/assets/img/莲花山3.jpeg">
            <img src="@/assets/img/莲花山4.jpeg">
            <img src="@/assets/img/莲花山5.jpeg">
            <p class="spike-title">
                {{item.title}}</p>
            <div class="bottom-price">
                <span>￥{{item.price}}</span>
                <p>剩下{{item.num}}个</p>
            </div>
        </div>
        </div>
    </swiper-slide>
    <div class="swiper-pagination" slot="pagination">
    </div>
  </swiper></div>
</template>

<script>
//定时器
import CountDown from 'vue2-countdown'
export default {
    props:['spikeList'],
    components:{
        CountDown
    },
       data () {
        return{
            
            //swiper
            swiperOption:{
               // loop:true,
               zoom : true,
                autoplay:true,
               loop:true,
                pagination:{
                     el: ".swiper-pagination",
                   
                }
            }
        }
        
    },
   methods:{
       todetails(item){
           this.$router.push({
               path:"/details",
               //传值
               query:{
                   item
               }
           })
       }
   }
    
}
</script>

<style scoped>
.spike{
    padding: .3rem;
    height: 4.37rem;
    
}
.spike-top{
    width: 100%;
    height: .2933333rem;
    display: flex;
    border: 1px solid white;
}
.spike-top span{
    margin-left: .15rem;
}
.spike-top > div{
    margin-left: .15rem;
}
.spike-top > div {
    margin-left: .15rem;
    color: #fff;
    border-radius: .1rem;
    background: #8b8b8b;
    padding: 0 .1rem;
}
.spike-botton{
    display: block;
    margin-bottom: 20px;
    justify-content: space-between;
   
}
.spike-botton img{
    width: 3.6rem;
    height: 2.8rem;
    border: 1px solid pink;
    margin-right: 1px;
}
.bottom-title p{
    height: 1rem;
    color: #333;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;
    font-size: .28rem;
     display: -webkit-box;
     text-align: center;

}
.bottom-price{
    margin-top: .5rem;
    display: -webkit-inline-box;
    justify-content: space-between;
   
} 
.bottom-price span{
    font-size: 20px;
    color: #fed101;
   
}
.bottom-price p{
    color: #444;
    border: 1px solid #444;
    border-radius: .1rem;
    background: #fed101;
    text-align: center;
    padding: .1rem;
    float: right;
     margin: 0 20px;
     
    
}
.spike-content{
    cursor: pointer;
}


</style>